import popModal from '../../../components/popmodal.vue'
export default{
	data(){
		return{
			designer:{
				account:'',
				token:''
			},
			isShow:false
		}
	},
	components:{
		popModal
	},
	onLoad(option){
		if(option){
			this.designer.account=option.account;
			this.designer.token=option.token;
		}
		
	},
	methods:{
		inputAccount(event){
			this.isShow=true;
		},
		inputPwd(event){
			this.isShow=true;
		},
		//注册
		toDesign(){
			uni.navigateTo({
				url:'../design/design'
			})
		},
		//忘记密码
		toFindPassword(){
			uni.navigateTo({
				url:'../find-password/find-password'
			})
		},
		loginEvent(){
			uni.request({
				url:this.$store.state.url+'Login',
				data:{
					user:this.designer.account,
					token:this.designer.token
				},
				success: (res) => {
					if(res.data.code == -1){
						uni.showToast({
							title: `账号或者密码错误`,
							mask: false,
							icon:'none',
							duration: 1500
						})
						
					}else{
						if(res.data.data.type!=4){
							uni.showToast({
								title: `您的账号无法在“门店助手”登录`,
								mask: false,
								icon:'none',
								duration: 1500
							})
						}else{
							this.$store.commit('setUserInfo',res.data.data);
							uni.setStorage({
								key: 'userInfo',
								data: res.data.data
							});
							uni.showToast({
								title:'登录成功',
								icon:'none'
							})
							setTimeout(function(){
								uni.switchTab({
									url:'../../tab-item/index/index'
								})
							},500)
						}
					}


				}
			})
		}
	}
}
